import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    /* Home */
    client: {},
    clientAll: [],
    /*  Sections */
    section: {
      tab: "Tableros",
      content: "BoardList",
      template: [

        //Plantilla  de los datos
      ],
      data: [
        /*  {
           reference: 1,
           nomenclature: "new tablero",
           location: "palapa",
            items: [1, 2, 1, 2, 1, { name: "hola" }],
         }, */
      ],
    },
    sectionAll: [
      {
        tab: "Tableros",
        content: "BoardList",
        template: [



          //Plantilla  de los datos
        ],
        data: [
          {
            reference: 1,
            nomenclature: "",
            location: "",
            /*  items: [1, 2, 1, 2, 1, { name: "hola" }], */
          },
        ],
      },

    ],
    /* Boards */
    board: {},
    boardAll: [
      /* {
        reference: 1,
        nomenclature: "new tablero",
        location: "Gym",
        items: [1, 2, 1, 2, 1, { name: "hola" }]
      }, */
      /*   {
          reference: 2,
          nomenclature: "new tablero",
          location: "palapa",
          items: [1, 2, 1, 2, 1, { name: "hola" }]
        }, */
    ],
  },
  getters: {
    /* Home */
    getClient(state) {
      state.boardAll = [];
      return state.client;
    },
    getClientAll(state) {
      return state.clientAll;
    },
    /*  Sections */
    getSection(state) {
      return state.sectionAll;
    },
    /* Boards */
    getBoard(state) {
      return state.board;
    },
    getBoardAll(state) {
      return state.boardAll;
    }
  },
  mutations: {
    /* Home */
    newClient(state, dt) {
      state.clientAll.unshift(dt);
      state.boardAll = [];
    },
    setClient(state, dt) {
      state.client = dt;
    },
    updateClient(state, dt) {
      console.log(dt);

      for (const obj of state.clientAll) {
        if (obj.id === dt.id) {
          console.log('mach');
          obj.tradename = dt.tradename;
          obj.municipality = dt.municipality;
          obj.date = dt.date;
          obj.whoAttendedUs = dt.whoAttendedUs;
          obj.typeOfOpinion = dt.typeOfOpinion;

          break;
        }
      }
      console.log(state.clientAll);
    },
    deleteClientAll(state) {
      state.clientAll = [];
    },
    /* Section */
    setSectionAll(state, dt) {
      state.sectionAll = dt;
    },
    /*  Board */
    newBoard(state, dt) {
      state.boardAll.push(dt);
    },
  },
  actions: {
    /* Home */
    newClientAction(context, dt) {
      context.commit('newClient', dt);
    },
    setClientAction(context, dt) {
      context.commit('setClient', dt);
    },
    updateClientAction(context, dt) {
      context.commit('updateClient', dt);

    },
    deleteClientAllAction(context) {
      context.commit('deleteClientAll');
    },
    setSectionAllAction(context, dt) {
      context.commit('setSectionALL', dt);
    },

    /* Board */
    newBoardAction(context, dt) {
      context.commit('newBoard', dt);
    }
  },
  modules: {}
})
